































import Vue from "vue";

// components
import MainHeader from "./MainHeader.vue";

export default Vue.extend({
  name: "PickerContainer",
  components: { MainHeader },
  props: {
    title: {
      type: String,
      default: undefined,
    },
    submitTitle: {
      type: String,
      default: undefined,
    },
    color: {
      type: String,
      default: "#188EF2",
    },
    modal: {
      type: Boolean,
      default: false,
    },
    showModal: {
      type: Boolean,
      default: false,
    },
  },
  mounted() {
    if (this.modal) {
      (this.$refs.container as HTMLDivElement).addEventListener(
        "wheel",
        this.onWheel
      );
    }
  },
  beforeDestroy() {
    if (this.modal) {
      (this.$refs.container as HTMLDivElement).removeEventListener(
        "wheel",
        this.onWheel
      );
    }
  },
  methods: {
    onWheel(event: Event) {
      event.preventDefault();
    },
  },
});
